import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import List from '@vkontakte/vkui/dist/components/List/List';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline';

const Forschool = ({ any, id, go }) => (
	<Panel id={id}>
			<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="home">
				{<Icon28ArrowLeftOutline />}
			</PanelHeaderButton>}
		>
			Для школы
		</PanelHeader>
        <Group header={<Header mode="secondary">Категории</Header>}>
              <List>
			  <Cell expandable onClick={go} data-to="sch1">Лайфхак #1</Cell> 
				<Separator />
                <Cell expandable onClick={() => this.setState({ activePanel: "persik" })}>Скоро что-то будет</Cell> 
                <Separator />
				<Div style={{ paddingTop: 0, paddingBottom: 0, color: 'grey' }}>
				<p align="center">Скоро тут появятся новые лайфхаки.</p>
				</Div>
              </List>
            </Group>
	</Panel>
);

Forschool.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Forschool;
