import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import List from '@vkontakte/vkui/dist/components/List/List';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';

const Home = ({ any, id, go }) => (
	<Panel id={id}>
			<PanelHeader
				 
			left={<PanelHeaderButton onClick={go} data-to="oservise">
				{<Icon28SettingsOutline/>} 
				</PanelHeaderButton>}
    >
			Категории
		</PanelHeader>

		<Group header={<Header mode="secondary">Категории</Header>}>
              <List>
				<Cell expandable before={<Icon16Fire />} onClick={go} data-to="forschool" indicator={<Counter >2</Counter>}>Лайфхаки для школы</Cell> 
				<Separator />
                <Cell expandable onClick={go} data-to="forsummer" indicator={<Counter mode="primary">0</Counter>}>Лайфхаки для лета</Cell> 
				<Separator />
                <Cell expandable onClick={go} data-to="forschool" indicator={<Counter>0</Counter>}>Лайфхаки для дома</Cell>
				<Separator />
                <Cell expandable onClick={go} data-to="forschool" indicator={<Counter mode="primary">0</Counter>}>Лайфхаки для машины</Cell>
				<Separator />
				<Div style={{ paddingTop: 0, paddingBottom: 0, color: 'grey' }}>
				<p align="center">Скоро тут появятся новые категории.</p>
				</Div>
              </List>
            </Group>

		<Group title="Navigation Example">
			<Div>
				<Button mode="commerce" size="xl" level="2" onClick={go} data-to="offer">
					Предложить лайфхак
				</Button>
			</Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
